import request from '@/utils/request';

/**
 * API - 
 */
const userApi = {
  /**
   * 
   */
  async getUserProfile() {
    console.log('getUserProfile API');
    try {
    const response = await request.get('/api/auth/users/me/');
      console.log('getUserProfile API:', response);
      
      // 
      if (response && response.success && response.data) {
        // {success: true, data: {...}}
    return response;
      } else if (response && response.code === 200 && response.data) {
        // {code: 200, data: {...}}
        return {
          success: true,
          data: response.data
        };
      } else if (response && typeof response === 'object' && response.id) {
        // 
        return {
          success: true,
          data: response
        };
      } else {
        console.error('getUserProfile API:', response);
        return {
          success: false,
          message: '',
          error: response
        };
      }
    } catch (error) {
      console.error('getUserProfile API:', error);
      return {
        success: false,
        message: error.message || '',
        error: error
      };
    }
  },

  /**
   * 
   * @param {Object} profileData - 
   */
  async updateUserProfile(profileData) {
    let userId = profileData.id;
    if (!userId) {
      const userJson = localStorage.getItem('user');
      if (userJson) {
        const user = JSON.parse(userJson);
        userId = user.id;
      }
    }
    
    if (!userId) {
      return {
        success: false,
        message: 'ID'
      };
    }
    
    const cleanedData = {
      username: profileData.username || '',
      name: profileData.name || '',
      email: profileData.email || '',
      phone: profileData.phone || '',
      position: profileData.position || '',
      department: profileData.department || '',
      office: profileData.office || '',
      employee_id: profileData.employee_id || '',
      manager: profileData.manager || '',
      bio: profileData.bio || ''
    };
    
    if (!cleanedData.username) {
      const userJson = localStorage.getItem('user');
      if (userJson) {
        const user = JSON.parse(userJson);
        if (user.username) {
          cleanedData.username = user.username;
        }
      }
    }
    
    console.log(`PUT: /api/auth/users/${userId}/`);
    console.log(':', JSON.stringify(cleanedData));
    
    try {
      const response = await request.put(`/api/auth/users/${userId}/`, cleanedData);
      
      return {
        success: true,
        message: '',
        data: response.data || response
      };
    } catch (error) {
      console.error('API:', error);
      
      let errorMessage = '';
      let errorFields = null;
      
      if (error.response) {
        console.error(':', error.response.status);
        console.error(':', error.response.data);
        
        if (error.response.data) {
          if (typeof error.response.data === 'string') {
            errorMessage = error.response.data;
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message;
          } else if (error.response.data.detail) {
            errorMessage = error.response.data.detail;
          } else if (error.response.data.error) {
            errorMessage = error.response.data.error;
          } else {
            errorFields = {};
            let hasFields = false;
            
            Object.entries(error.response.data).forEach(([field, messages]) => {
              if (Array.isArray(messages) && messages.length > 0) {
                errorFields[field] = messages;
                hasFields = true;
                
                if (field === 'username' && messages.includes('')) {
                  errorMessage = '';
                } else if (field === 'email' && messages.includes('')) {
                  errorMessage = '';
                }
              }
            });
            
            if (hasFields) {
              const fieldErrors = Object.entries(errorFields)
                .map(([field, msgs]) => `${field}: ${msgs.join(', ')}`)
                .join('; ');
              
              if (fieldErrors) {
                errorMessage = fieldErrors;
              }
            }
          }
        }
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      return {
        success: false,
        message: errorMessage,
        fields: errorFields,
        error: error
      };
    }
  },

  /**
   * 
   * @param {FormData} formData - 
   */
  async updateUserAvatar(formData) {
    const userId = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).id : null;
    if (!userId) {
      throw new Error('');
    }
    const response = await request.post(`/api/auth/users/${userId}/avatar/`, formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
    return response;
  },

  /**
   * 
   * @param {Object} passwordData - 
   */
  async changePassword(passwordData) {
    const { old_password, new_password, confirm_password } = passwordData;
    
    // 
    const requestData = {
      old_password,
      new_password,
      confirm_password  // 
    };
    
    console.log('');
    
    try {
      const response = await request.put('/api/auth/users/change_password/', requestData);
      console.log('');
      return {
        success: true,
        message: '',
        data: response
      };
    } catch (error) {
      // 
      if (error.isValidationResult) {
        console.log('');
        return {
          success: false,
          message: '',
          fieldErrors: error.fieldErrors || error.data
        };
      }
      
      // 
      console.error('');
      
      // 
      if (error.response) {
        console.error(':', error.response.status);
        
        const data = error.response.data;
        
        // 
        if (data && data.message && (
            data.message.toLowerCase().includes('success') || 
            data.message.toLowerCase().includes('successfully')
          )) {
          console.log('');
          return {
            success: true,
            message: '',
            data: data
          };
        }
        
        // 
        const isOldPasswordError = data && 
          data.old_password && 
          Array.isArray(data.old_password) && 
          data.old_password.includes("Incorrect password.");
        
        // 
        let errorMessage = '';
        
        if (typeof data === 'string') {
          errorMessage = data;
        } else if (data) {
          if (data.message) {
            errorMessage = data.message;
          } else if (data.detail) {
            errorMessage = data.detail;
          } else if (data.non_field_errors && Array.isArray(data.non_field_errors)) {
            errorMessage = data.non_field_errors.join(', ');
          } else if (data.old_password && Array.isArray(data.old_password)) {
            // 
            if (data.old_password.includes("Incorrect password.")) {
              errorMessage = "";
            } else {
              errorMessage = `: ${data.old_password.join(', ')}`;
            }
          } else if (data.new_password && Array.isArray(data.new_password)) {
            // 
            const newPasswordErrors = data.new_password.map(err => {
              if (err.includes("too short")) return "";
              if (err.includes("too common")) return "";
              if (err.includes("entirely numeric")) return "";
              if (err.includes("similar to")) return "";
              return err;
            });
            errorMessage = `: ${newPasswordErrors.join(', ')}`;
          } else {
            // 
            const errorDetails = [];
            try {
              Object.entries(data).forEach(([field, messages]) => {
                // 
                let fieldName = field;
                if (field === 'old_password') fieldName = '';
                if (field === 'new_password') fieldName = '';
                if (field === 'confirm_password') fieldName = '';
                
                if (Array.isArray(messages) && messages.length > 0) {
                  errorDetails.push(`${fieldName}: ${messages.join(', ')}`);
                } else if (typeof messages === 'string') {
                  errorDetails.push(`${fieldName}: ${messages}`);
                } else if (messages !== null && typeof messages === 'object') {
                  errorDetails.push(`${fieldName}: ${JSON.stringify(messages)}`);
                }
              });
            } catch (e) {
              console.error(':', e);
            }
            
            if (errorDetails.length > 0) {
              errorMessage = errorDetails.join('; ');
            }
          }
        }
        
        return {
          success: false,
          message: isOldPasswordError ? '' : errorMessage, // 
          fieldErrors: data  // 
        };
      }
      
      return {
        success: false,
        message: error.message || ''
      };
    }
  },

  /**
   * 
   * @param {number} companyId - ID
   * @returns {Promise} Promise
   */
  async getCompanyUsers(companyId) {
    try {
      if (!companyId) {
        // ID
        const userJson = localStorage.getItem('user');
        if (userJson) {
          const user = JSON.parse(userJson);
          companyId = user.company;
        }
        
        // ID
        if (!companyId) {
          return {
            success: false,
            message: 'ID'
          };
        }
      }
      
      const response = await request.get(`/api/auth/companies/${companyId}/users/`);
      
      let users = [];
      if (Array.isArray(response.data)) {
        users = response.data;
      } else if (response.data && Array.isArray(response.data.data)) {
        users = response.data.data;
      } else if (response.data && response.data.results && Array.isArray(response.data.results)) {
        users = response.data.results;
      }
      
      return {
        success: true,
        data: users
      };
    } catch (error) {
      console.error(':', error);
      return {
        success: false,
        message: error.message || '',
        error: error
      };
    }
  }
};

export default userApi; 