import authApi from '@/api/auth';
import userApi from '@/api/user';

const state = {
  token: localStorage.getItem('token') || '',
  user: JSON.parse(localStorage.getItem('user')) || null,
  loading: false,
  error: null
};

const mutations = {
  AUTH_REQUEST(state, loading = true) {
    state.loading = loading;
    if (loading) {
      state.error = null;
    }
  },
  AUTH_SUCCESS(state, { token, user }) {
    state.loading = false;
    state.token = token;
    state.user = user;
    state.error = null;
  },
  AUTH_ERROR(state, error) {
    state.loading = false;
    state.error = error;
  },
  LOGOUT(state) {
    state.token = '';
    state.user = null;
  },
  CLEAR_ERROR(state) {
    state.error = null;
  },
  UPDATE_USER_PROFILE(state, userData) {
    state.user = { ...state.user, ...userData };
    localStorage.setItem('user', JSON.stringify(state.user));
  },
  SET_USER_PROFILE(state, profile) {
    state.user = { ...state.user, ...profile };
    localStorage.setItem('user', JSON.stringify(state.user));
  }
};

const actions = {
  // 
  async login({ commit, dispatch }, credentials) {
    commit('AUTH_REQUEST');
    try {
      const response = await authApi.login(credentials);
      
      if (response.success) {
        const { token, userId, username } = response.data;
        
        // 
        localStorage.setItem('token', token);
        
        // 
        const user = {
          id: userId,
          username: username
        };
        
        // 
        localStorage.setItem('user', JSON.stringify(user));
        
        commit('AUTH_SUCCESS', { token, user });
        
        // 
        dispatch('getUserProfile');
        
        return Promise.resolve(response);
      } else {
        commit('AUTH_ERROR', response.message || '');
        return Promise.reject(response);
      }
    } catch (error) {
      let errorMsg = '';
      
      // 
      if (error.response) {
        if (error.response.data) {
          if (error.response.data.message) {
            errorMsg = error.response.data.message;
          } else if (error.response.data.detail) {
            errorMsg = error.response.data.detail;
          } else if (typeof error.response.data === 'string') {
            errorMsg = error.response.data;
          }
        } else if (error.response.statusText) {
          errorMsg = ` (${error.response.status}): ${error.response.statusText}`;
        }
      } else if (error.message) {
        errorMsg = error.message;
      }
      
      commit('AUTH_ERROR', errorMsg);
      return Promise.reject({ ...error, message: errorMsg });
    }
  },
  
  // 
  async adminLogin({ commit, dispatch }, credentials) {
    commit('AUTH_REQUEST');
    try {
      const response = await authApi.adminLogin(credentials);
      
      if (response.success) {
        const { token, userId, username } = response.data;
        
        localStorage.setItem('token', token);
        
        const user = {
          id: userId,
          username: username,
          isAdmin: true 
        };
        
        localStorage.setItem('user', JSON.stringify(user));
        
        commit('AUTH_SUCCESS', { token, user });
        
        dispatch('getUserProfile');
        
        return Promise.resolve(response);
      } else {
        commit('AUTH_ERROR', response.message || '');
        return Promise.reject(response);
      }
    } catch (error) {
      let errorMsg = '';
      
      if (error.response && error.response.data && error.response.data.message) {
        errorMsg = error.response.data.message;
      } else if (error.message) {
        errorMsg = error.message;
      }
      
      commit('AUTH_ERROR', errorMsg);
      return Promise.reject({ ...error, message: errorMsg });
    }
  },
  
  // 
  async register({ commit }, userData) {
    commit('AUTH_REQUEST');
    try {
      const response = await authApi.register(userData);
      
      if (response.success) {
        return Promise.resolve(response);
      } else {
        commit('AUTH_ERROR', response.message);
        return Promise.reject(response);
      }
    } catch (error) {
      const errorMsg = error.response?.data?.message || '';
      commit('AUTH_ERROR', errorMsg);
      return Promise.reject(error);
    }
  },
  
  // 
  async logout({ commit }) {
    try {
      await authApi.logout();
    } catch (error) {
      console.error('', error);
    }
    
    // 
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    
    // 
    localStorage.setItem('theme', 'light');
    document.documentElement.setAttribute('data-theme', 'light');
    
    // meta
    const metaThemeColor = document.querySelector('meta[name="theme-color"]');
    if (metaThemeColor) {
      metaThemeColor.setAttribute('content', '#ffffff');
    }
    
    // 
    commit('LOGOUT');
    
    return Promise.resolve();
  },
  
  // 
  async requestPasswordReset({ commit }, { email }) {
    commit('AUTH_REQUEST');
    try {
      const response = await authApi.requestPasswordReset({ email });
      
      if (response.success) {
        commit('AUTH_SUCCESS', { token: '', user: null });
        return Promise.resolve(response);
      } else {
        commit('AUTH_ERROR', response.message);
        return Promise.reject(response);
      }
    } catch (error) {
      const errorMsg = error.response?.data?.message || '';
      commit('AUTH_ERROR', errorMsg);
      return Promise.reject(error);
    }
  },
  
  // 
  async resetPassword({ commit }, resetData) {
    commit('AUTH_REQUEST');
    try {
      const response = await authApi.resetPassword(resetData);
      
      if (response.success) {
        commit('AUTH_SUCCESS', { token: '', user: null });
        return Promise.resolve(response);
      } else {
        commit('AUTH_ERROR', response.message);
        return Promise.reject(response);
      }
    } catch (error) {
      const errorMsg = error.response?.data?.message || '';
      commit('AUTH_ERROR', errorMsg);
      return Promise.reject(error);
    }
  },
  
  // 
  async getUserProfile({ commit }) {
    commit('AUTH_REQUEST');
    try {
      console.log('');
      const response = await userApi.getUserProfile();
      console.log(':', response);
      
      // 
      let userData = null;
      
      if (response && response.success && response.data) {
        //  {success: true, data: {...}}
        console.log('');
        userData = response.data;
      } else if (response && response.code === 200 && response.data) {
        //  {code: 200, data: {...}}
        console.log('code=200');
        userData = response.data;
      } else if (response && typeof response === 'object' && response.id) {
        //  {id: ..., username: ...}
        console.log('');
        userData = response;
      } else {
        console.error(':', response);
        commit('AUTH_ERROR', '');
        return Promise.reject({message: ''});
      }
      
      // ID
      if (!userData.company) {
        console.warn('ID:', userData);
      }
      
      commit('SET_USER_PROFILE', userData);
      console.log('VuexlocalStorage');
      return Promise.resolve({success: true, data: userData});
    } catch (error) {
      console.error(', :', error);
      let errorMsg = '';
      
      if (error.response) {
        console.error(':', error.response.status);
        console.error(':', error.response.data);
        
        if (error.response.status === 403) {
          errorMsg = '';
        } else if (error.response.data && typeof error.response.data === 'object') {
          if (error.response.data.message) {
            errorMsg = error.response.data.message;
          } else if (error.response.data.detail) {
            errorMsg = error.response.data.detail;
          }
        }
      } else if (error.message) {
        errorMsg = error.message;
      }
      
      commit('AUTH_ERROR', errorMsg);
      return Promise.reject({...error, message: errorMsg});
    }
  },
  
  // 
  async updateUserProfile({ commit }, profileData) {
    commit('AUTH_REQUEST');
    try {
      // API
      console.log('API:', profileData);
      
      const response = await userApi.updateUserProfile(profileData);
      
      if (response.success) {
        commit('UPDATE_USER_PROFILE', response.data);
        return Promise.resolve(response);
      } else {
        const errorMsg = response.message || '';
        commit('AUTH_ERROR', errorMsg);
        return Promise.reject(errorMsg);
      }
    } catch (error) {
      // 
      console.error(':', error);
      
      let errorMsg = '';
      
      // 
      if (typeof error === 'string') {
        errorMsg = error;
      } else if (error.message) {
        errorMsg = error.message;
      } else if (error.response && error.response.data) {
        if (typeof error.response.data === 'string') {
          errorMsg = error.response.data;
        } else if (error.response.data.message) {
          errorMsg = error.response.data.message;
        } else if (error.response.data.detail) {
          errorMsg = error.response.data.detail;
        } else if (error.response.data.error) {
          errorMsg = error.response.data.error;
        } else {
          // 
          const errorFields = [];
          for (const field in error.response.data) {
            if (Array.isArray(error.response.data[field])) {
              errorFields.push(`${field}: ${error.response.data[field].join(', ')}`);
            }
          }
          if (errorFields.length > 0) {
            errorMsg = errorFields.join('; ');
          }
        }
      }
      
      commit('AUTH_ERROR', errorMsg);
      
      // 
      return Promise.reject(errorMsg);
    }
  },
  
  // 
  async updateUserAvatar({ commit }, formData) {
    commit('AUTH_REQUEST');
    try {
      const response = await userApi.updateUserAvatar(formData);
      
      if (response.success) {
        commit('UPDATE_USER_PROFILE', { avatar: response.data.avatar });
        return Promise.resolve(response);
      } else {
        commit('AUTH_ERROR', response.message || '');
        return Promise.reject(response);
      }
    } catch (error) {
      const errorMsg = error.response?.data?.message || '';
      commit('AUTH_ERROR', errorMsg);
      return Promise.reject(error);
    }
  },
  
  // 
  async changePassword({ commit }, passwordData) {
    commit('AUTH_REQUEST');
    try {
      const response = await userApi.changePassword(passwordData);
      
      if (response.success) {
        commit('CLEAR_ERROR');
        return Promise.resolve(response);
      } else {
        // 
        const isOldPasswordError = response.fieldErrors && 
          response.fieldErrors.old_password && 
          Array.isArray(response.fieldErrors.old_password) && 
          response.fieldErrors.old_password.includes("Incorrect password.");
          
        // 
        if (!isOldPasswordError) {
          commit('AUTH_ERROR', response.message || '');
        } else {
          // 
          commit('CLEAR_ERROR');
        }
        
        return response; // Promise.reject
      }
    } catch (error) {
      // 
      if (error.isValidationResult) {
        // 
        commit('CLEAR_ERROR');
        // 
        return error;
      }
      
      // 
      const isOldPasswordError = error.fieldErrors && 
        error.fieldErrors.old_password && 
        Array.isArray(error.fieldErrors.old_password) && 
        error.fieldErrors.old_password.includes("Incorrect password.");
        
      // 
      if (!isOldPasswordError) {
        const errorMsg = error.response?.data?.message || '';
        commit('AUTH_ERROR', errorMsg);
      } else {
        // 
        commit('CLEAR_ERROR');
      }
      
      return Promise.reject(error);
    } finally {
      // loading
      commit('AUTH_REQUEST', false);
    }
  },
  
  // 
  clearError({ commit }) {
    commit('CLEAR_ERROR');
  }
};

const getters = {
  isAuthenticated: state => !!state.token,
  currentUser: state => state.user,
  loading: state => state.loading,
  error: state => state.error
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}; 