import axios from 'axios';
import { ElMessage } from 'element-plus';

// axios
const service = axios.create({
  baseURL: 'http://localhost:8000',  // 
  timeout: 180000,  // 60180AI
  headers: {
    'Content-Type': 'application/json'
  }
});

//  - 
const getSmartTimeout = (config) => {
  const url = config.url || '';
  const method = config.method || '';
  const data = config.data;
  
  // 3
  let timeout = 180000;
  
  // AI
  if (url.includes('ai_generate_document')) {
    timeout = 180000;
  }
  // AI
  else if (url.includes('ai_polish') || url.includes('check_contract')) {
    if ((method === 'post' || method === 'put') && data) {
      const contentSize = typeof data === 'string' ? 
                          data.length : 
                          (data.content ? data.content.length : 0);
      if (contentSize > 50000) {
        timeout = 180000;
      } else if (contentSize > 20000) {
        timeout = 120000;
      } else if (contentSize > 5000) {
        timeout = 90000;
      }
      console.log(`:  ${contentSize} ,  ${timeout/1000} `);
    } else {
      timeout = 120000;
    }
  } else if (url.includes('contract') && method === 'post' && data && data.use_ai_agent) {
    console.log('AI');
    timeout = 180000;
  }
  
  return timeout;
};

//  - token
service.interceptors.request.use(
  config => {
    console.log(':', config.url, ':', config.method);
    
    // 
    config.timeout = getSmartTimeout(config);
    
    // 
    if (config.data instanceof FormData) {
      const hasFile = Array.from(config.data.entries()).some(entry => entry[1] instanceof File);
      if (hasFile) {
        console.log('');
        config.timeout = Math.max(config.timeout, 180000); // 3
      }
    }
    
    // AbortSignal
    if (config.signal) {
      console.log(' (AbortSignal)');
    }
    
    // signal
    if (config.data && config.data.signal) {
      config.signal = config.data.signal;
      // signal
      delete config.data.signal;
      console.log('AbortSignal');
    }
    
    const requireAuth = config.auth !== false; // 
    const token = localStorage.getItem('token'); //  localStorage  token
    console.log('Token from localStorage:', token ? '' : '');
    
    if (token && requireAuth) {
      // BearerToken
      config.headers.Authorization = `Bearer ${token}`; 
      console.log('token (: Bearer ...) :', config.url);
      
      // 
      if (config.url.includes('export') || config.url.includes('export-direct')) {
        console.log(':', config.headers.Authorization ? '' : '');
      }
    } else if (!requireAuth) {
      console.log(':', config.url, ':', config.method);
    } else {
      console.warn('tokenAuthorization:', config.url);
      
      // 
      if (config.url.includes('export') || config.url.includes('export-direct')) {
        console.warn('');
      }
    }
    
    // FormDataContent-Type
    if (config.data instanceof FormData) {
      delete config.headers['Content-Type'];
      // 
      if (config.url.includes('/api/chat/messages/')) {
        console.log('FormData:');
        for (let [key, value] of config.data.entries()) {
          if (value instanceof File) {
            console.log(`- ${key}: [] ${value.name}, : ${value.type}, : ${value.size} `);
          } else {
            console.log(`- ${key}: ${value}`);
          }
        }
      }
    } else if (config.method === 'post' || config.method === 'put') {
      if (!config.headers['Content-Type']) {
        config.headers['Content-Type'] = 'application/json';
      }
    }
    
    return config;
  },
  error => {
    console.error(':', error);
    return Promise.reject(error);
  }
);

//  - 
service.interceptors.response.use(
  response => {
    console.log(':', response.config.url, response.status)
    
    //  204 No Content 
    if (response.status === 204) {
      // 204
      return { status: 204 };
    }
    
    // API
    if (response.config.url.includes('/api/smartdoc/documents/') && 
        response.config.url.endsWith('/') &&
        !response.config.url.includes('list')) {
      // 
      const contentLength = response.headers['content-length'] || '';
      console.log(':', contentLength, '');
      
      // 
      if (contentLength && parseInt(contentLength) > 100000) {
        console.log(':', {
          url: response.config.url,
          size: contentLength + ' ',
          status: response.status,
          headers: response.headers,
          time: new Date().toISOString()
        });
      }
    }
    
    // API
    if (response.config.url.includes('check_contract') || 
        response.config.url.includes('ai_polish') ||
        (response.config.url.includes('contracts') && response.config.method === 'post')) {
      console.log('API:', {
        url: response.config.url,
        status: response.status,
        headers: response.headers,
        dataType: typeof response.data,
        dataHasKeys: typeof response.data === 'object' ? Object.keys(response.data).join(',') : 'not-an-object',
        responseTime: new Date().toISOString()
      });
    }
    
    const res = response.data;
    
    // 
    if (res) {
      const type = typeof res;
      const isArray = Array.isArray(res);
      console.log(':', isArray ? 'Array' : type, 
                  isArray ? `(:${res.length})` : 
                  (type === 'object' ? `(:${Object.keys(res).join(',')})` : ''));
                  
      // 
      if (response.config.url.includes('/api/smartdoc/documents/') && 
          response.config.url.endsWith('/') &&
          type === 'object' && 
          res.content === undefined && 
          res.id) {
        console.warn(': content');
      }
    }
    
    // 
    if (response.config.responseType === 'blob') {
      console.log('blob:', response.data.size, '');
      // blob
      if (response.data.size < 100) {
        console.warn('blob:', response.data.size, '');
      }
      return response;
    }
    
    // DRF
    const isDRFPagination = res && !Array.isArray(res) && res.results && Array.isArray(res.results);
    if (isDRFPagination) {
      console.log('DRF:', 
                  'count=', res.count, 
                  'next=', Boolean(res.next), 
                  'previous=', Boolean(res.previous),
                  '=', res.results.length);
      // dataresults
      return {
        count: res.count,
        total: res.count, // total
        next: res.next,
        previous: res.previous,
        results: res.results,
        data: res.results
      };
    }
    
    //  { data: [...] } 
    // 
    if (response.data) {
      //  [...]
      if (Array.isArray(response.data)) {
        console.log('');
        return {
          data: response.data,
          count: response.data.length, // 
          total: response.data.length   // 
        };
      }
      //  {...}
      else if (typeof response.data === 'object') {
        console.log('');
        // datadata
        if ('data' in response.data) {
          return response.data;
        } else {
          return {
            data: response.data
          };
        }
      }
    }
    
    // 
    return { data: {} };
  },
  error => {
    // 
    if (axios.isCancel(error)) {
      console.log(':', error.message);
      // 
      return Promise.reject({
        isCancelled: true,
        message: ''
      });
    }
    
    //  - 
    if (error.response && error.response.status === 400) {
      const data = error.response.data;
      const isPasswordChangeEndpoint = error.config.url.includes('/api/auth/users/change_password/');
      const isOldPasswordIncorrect = data && 
        data.old_password && 
        Array.isArray(data.old_password) && 
        data.old_password.includes("Incorrect password.");
      
      // 
      if (isPasswordChangeEndpoint && isOldPasswordIncorrect) {
        console.log(' - ');
        // 
        return Promise.reject({
          isValidationResult: true,
          status: 400,
          data: error.response.data,
          message: '',
          fieldErrors: {
            old_password: ['Incorrect password.']
          }
        });
      }
    }

    // 
    console.error(':', error);
    
    // 
    if (error.code === 'ECONNABORTED' || (error.message && error.message.includes('timeout'))) {
      console.error(':', error.config.url);
      ElMessage.error('');
      return Promise.reject({
        isTimeout: true,
        message: ''
      });
    }
    
    // HTTP
    if (error.response) {
      const status = error.response.status;
      
      // blob
      if (error.config && error.config.responseType === 'blob') {
        console.error('Blob:', status, error.config.url);
        
        // blob
        if (error.response.data instanceof Blob) {
          const blob = error.response.data;
          // JSON
          if (blob.type === 'application/json') {
            const reader = new FileReader();
            reader.onload = () => {
              try {
                const errorData = JSON.parse(reader.result);
                console.error('Blob:', errorData);
                ElMessage.error(errorData.error || errorData.message || '');
              } catch (e) {
                console.error('Blob:', e);
                ElMessage.error('');
              }
            };
            reader.readAsText(blob);
          } else {
            ElMessage.error('');
          }
          
          return Promise.reject({
            isBlobError: true,
            status,
            message: ''
          });
        }
      }
      
      // 
      if (status === 401) {
        ElMessage.error('');
        localStorage.removeItem('token');
        localStorage.removeItem('user');
        
        // 
        const currentPath = window.location.pathname;
        if (currentPath !== '/login' && currentPath !== '/') {
          setTimeout(() => {
            window.location.href = '/login';
          }, 1500);
        }
      } 
      // 
      else if (status === 403) {
        ElMessage.error('');
        localStorage.removeItem('token');
        localStorage.removeItem('user');
        
        // 
        const currentPath = window.location.pathname;
        if (currentPath !== '/login' && currentPath !== '/') {
          setTimeout(() => {
            window.location.href = '/login';
          }, 1500);
        }
      } 
      // 
      else if (status === 400) {
        const data = error.response.data;
        console.error('400:', data);
        console.error('400:', JSON.stringify(data, null, 2));
        
        // 400
        // 
        let message = '';
        if (data && data.message) {
          message = data.message;
        } else if (data && data.error) {
          message = data.error;
        } else if (typeof data === 'string') {
          message = data;
        } else if (data && typeof data === 'object') {
          // 
          message = Object.entries(data)
            .map(([key, value]) => `${key}: ${Array.isArray(value) ? value.join(', ') : value}`)
            .join('; ');
        }
        ElMessage.error(message);
      } 
      // 
      else if (status >= 500) {
        ElMessage.error('');
      } 
      // 
      else {
        try {
          ElMessage.error(error.response.data && error.response.data.message ? error.response.data.message : '');
        } catch (e) {
          ElMessage.error('');
        }
      }
    } else if (error.request) {
      // 
      ElMessage.error('');
    } else {
      // 
      ElMessage.error('');
    }
    
    return Promise.reject(error);
  }
);

export default service; 